<template>
  <div>
    <el-row style="margin-left: 25px">
  <el-col>
    <el-tabs v-model="activeName" >
      <el-tab-pane label="" name=""></el-tab-pane>
      <el-tab-pane label="" name=""></el-tab-pane>
      <el-tab-pane label="" name=""></el-tab-pane>
      <el-tab-pane label="" name=""></el-tab-pane>
    </el-tabs>
    <el-row v-for="report in reports" style="margin-top: 10px;padding: 10px 0;border-bottom: 1px dashed darkgrey" :gutter="12">
      <el-col :span="2" >
        <div class="like" @click="report.likes++" style="width: 50px;border-radius: 7px">
          <div style="width: 100%;line-height: 30px;text-align: center;background-color: #e9e9eb">
            <span>{{report.likes}}</span>
          </div>
          <div style="width: 100%;line-height: 18px;font-size: 10px;text-align: center;background-color: #f5dab1;height: 18px">
            <span></span>
          </div>
        </div>
      </el-col>
      <el-col :span="18">
        <h3 class="report-title">{{report.title}}</h3>
        <p class="report-main">{{report.main}}</p>
        <p class="information">{{report.author}}  -  ({{report.comment}}) -  ({{report.view}}) - theme ({{report.theme}})</p>
      </el-col>
      <el-col :span="3">
        <img src="../../images/nav-logo-A.jpeg" style="width: 100%;height: 100%;">
      </el-col>
    </el-row>
  </el-col>
    </el-row>
  </div>
</template>

<script>
    export default {
        name: "show",
        data(){
            return{
                activeName:'',
                reports:[
                    {
                        likes:1,
                        title:" ",
                        main:" 12  2  " +
                            "",
                        author:'',
                        comment:0,
                        view:0,
                        theme:''
                    }
                ]
            }
        }
    }
</script>

<style scoped>
  ul{
    font-size: 13px;
    margin: 0;
    padding: 0;
    list-style: none;
  }
  li{
    line-height: 20px;
    color: darkgrey;
  }
  .report-title{
    font-size: 16px;
    line-height: 20px;
    margin: 0;
  }
  .report-main{
    font-size: 13px;
    line-height: 15px;
  }
  .information{
    font-size: 12px;
    color: rgb(100,100,100);
    line-height: 20px;
  }
</style>



