<template>
  <div id="wangeditor">
    <el-row>
      <el-col style="margin-left: 15px;margin-top: 10px">
        <router-link to="/log/user"></router-link>
      </el-col>
    </el-row>
    <el-row>
      <el-col style="text-align: center;margin-top: 80px;font-family: 'Heiti SC'">
        <h2>Tell Me What You Thought</h2>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="16" :offset="4">
        <div style="text-align: right">
        <select id="theme">
          <option value =".Net ">.Net </option>
          <option value =""></option>
          <option value=""></option>
          <option value="Web">Web</option>
        <option value =""></option>
        <option value =""></option>
        <option value=""></option>
        <option value=""></option>
        <option value =""></option>
        <option value =""></option>
        <option value =""></option>
        <option value=""></option>
        </select>
        </div>
        <input id="title" type="text" style="width: 936px;padding: 10px;font-size: 18px;font-weight:bold;font-family: 'Heiti SC'" placeholder="">
        <div  ref="editorElem" style="text-align:left;"></div>
        <button type="button" style="width: 100%" @click="getHTML" id="login_click"><a style="width: 100%"></a></button>
      </el-col>
    </el-row>

  </div>
</template>

<script>
  import E from "wangeditor"
    export default {
        name: "WriteBlog",
        beforeCreate(){
            this.$router.push(0)
        },
        data(){
            return{
                data(){
                    return{
                        theme:'',
                        word:'',
                        dialogVisible: true
                    }
                },
                created(){
                    const theme = this.$route.path.split('/')[this.$route.path.split('/').length-1];
                    this.theme = theme;
                },
                methods:{
                    goback(){
                        this.$router.push('/log/blog/'+this.$route.path.split('/')[this.$route.path.split('/').length-2]);
                        this.theme = this.$route.path.split('/')[this.$route.path.split('/').length-1]
                    }
                },
                watch:{
                    $route:{
                        handler:function (val) {
                            this.theme = val.path.split('/')[val.path.split('/').length-1];
                            console.log(this.theme)
                        }
                    }
                }
            }

          },
        mounted() {
            this.editor = new E(this.$refs.editorElem);
            // html
            this.editor.customConfig.onchange = html => {
                this.editorContent = html;
                this.catchData(this.editorContent); // htmlcatchData
            };
            this.editor.customConfig.menus = [
                // 
                'head', // 
                'bold', // 
                'fontSize', // 
                'fontName', // 
                'italic', // 
                'underline', // 
                'strikeThrough', // 
                'foreColor', // 
                'backColor', // 
                'link', // 
                'list', // 
                'justify', // 
                'quote', // 
                'emoticon', // 
                'image', // 
                'table', // 
                'code', // 
                'undo', // 
                'redo' // 
            ];
            this.editor.create(); // 
        },
        methods:{
            getHTML(){
                document.getElementById('theme').value
                this.$axios.post('/user/postblog',{
                    title:document.getElementById('title').value,
                    html:this.editor.txt.html(),
                    theme:document.getElementById('theme').value,
                    text:this.editor.txt.text()
                }).then(res => {
                    console.log(res);
                    alert("")
                    this.$router.push('/');
                }).catch( error => {
                    console.log(error)
                })
            }
        },
        created() {

        }
    }



</script>

<style scoped>
  a{
    color: #ffffff;
  }


  #login_click{  height:40px;}
  #login_click
  {


    text-decoration:none;
    background:#2f435e;
    color:#f2f2f2;
    font-size:16px;
    font-family: ,,Arial,Helvetica,Verdana,sans-serif;
    font-weight:bold;
    border-radius:3px;

    -webkit-transition:all linear 0.30s;
    -moz-transition:all linear 0.30s;
    transition:all linear 0.30s;

  }
  #login_click a:hover { background:#385f9e; }

</style>



