<template>
  <div>
  <el-tabs v-model="activeName">
    <el-tab-pane label="" name=""><router-link to="/question/questionlist"></router-link>
      <router-link to="/question/highscore"></router-link><router-link to="/question/new"></router-link>
      <router-link to="/question/new"></router-link></el-tab-pane>
    <el-tab-pane label="" name=""></el-tab-pane>
    <el-tab-pane label="" name=""></el-tab-pane>
  </el-tabs>
    <el-row>
      <el-col :span="19">
        <router-view></router-view>
      </el-col>
      <el-col :span="5">
        <div class="logo">
          <router-link to="/writequestion">
          <img src="../../images/askqesbg_h.png">
          </router-link>
          <p class="rankP">
            :{{user.douNumber}}<br>
            :{{user.wang}}<br>
            :{{user.rank}}
          </p>
        </div>
        <div class="searchQuestion" style="margin-left: 7px">
          <h3></h3>
          <el-input size="mini" v-model="searchQuestion" suffix-icon="el-icon-search"></el-input><el-button size="mini" type="text" @click="search"></el-button>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
    import PersonNav from "./personNav";
    export default {
        name: "Question",
        components: {PersonNav},
        data(){
            return{
                activeName:this.$route.params.theme,
                searchQuestion:'',
                user:{

                }
            }
        },
        watch:{
            activeName:{
                handler:function (val, oldVal) {
                    this.$router.push('/question/'+val)
                }
            },
            $route:{
                handler:function (val) {
                    this.activeName = val.params.theme
                    console.log(this.activeName);
                }
            }
        },
        methods:{
            search(){
                this.$router.push({
                    path:'/question/',
                    query:{
                      key:this.searchQuestion
                    }
                });
            }
        }
    }
</script>

<style scoped>
  h3{
    margin: 0 0 3px 0;
    font-size: 13px;

  }
  .rankP{
    font-size: 9px;
    color: darkgray;
    margin-left: 7px;
  }
  a{
    font-size: 15px;
    font-family: "Heiti SC";
    text-decoration: none;
    letter-spacing: 2px;
    padding-right: 6px;
    border-right: 1px solid darkgray;
    padding-left: 5px;
  }

  a:link{
    color: #2b6695;
  }

  a:visited{
    color: #2b6695;
  }
  a:hover{
    color: darkgray;
  }

  a:active{
    color: darkgray;
  }
</style>



